@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&family=Shadows+Into+Light&family=Passion+One&display=swap);

:root {
  --lightItem: #888;
  --hoverItem: #111;
}

html,
body,
#root,
.App {
  width: 100%;
  min-height: 100vh;
}

.App {
  display: flex;
  flex-direction: column;
  /* background: url("/Polka-Dots.svg"); */
  /* background: url("/Polka-Dots.svg"); */
}

.library-item:hover {
  color: #111;
  color: var(--hoverItem);
}
.library-item {
  cursor: pointer;
  margin-bottom: 7px;
  color: #888;
  color: var(--lightItem);
}

body {
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-size: 1.3rem;
  color: #888;
  color: var(--lightItem);
}

.adventure-logo {
  padding: 12px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 30px;
}

.player-logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 20px 0 0 20px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.player-button {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.widget-container {
  display: flex;
  flex-direction: column;
  min-width: 200px;
  position: absolute;
  cursor: move;
  border: 1px solid black;
  background-color: white;
  min-height: 200px;
  box-shadow: 0 1.6px 3.6px 0 rgba(0, 0, 0, 0.132),
    0 0.3px 0.9px 0 rgba(0, 0, 0, 0.108);
}
.joke-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 10px 10px 10px;
}
@media screen and (max-width: 350px) {
  .default {
    width: 150px;
  }
}
.default {
  width: 350px;
}
.getJoke {
  background-color: rgb(117, 192, 141);
  border: none;
  color: white;
  font-size: 1em;
  padding: 5px 10px;
  font-family: "Shadows Into Light", cursive;
  -webkit-animation-name: bounce-7;
          animation-name: bounce-7;
  -webkit-animation-timing-function: cubic-bezier(0.28, 0.84, 0.42, 1);
          animation-timing-function: cubic-bezier(0.28, 0.84, 0.42, 1);
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}
.title {
  text-align: center;
  color: red;
  font-size: 1em;
  font-family: "Permanent Marker", cursive;
  -webkit-animation: changeColor 8s infinite;
          animation: changeColor 8s infinite;
}
.Joke {
  text-align: center;
  font-family: "Passion One", cursive;
  font-size: 1em;
  padding-top: 5px;
}
@-webkit-keyframes changeColor {
  0% {
    color: rgb(241, 55, 55);
  }
  25% {
    color: rgb(214, 139, 26);
  }
  50% {
    color: rgb(46, 146, 212);
  }
  75% {
    color: rgb(202, 27, 150);
  }
  100% {
    color: rgb(241, 55, 55);
  }
}
@keyframes changeColor {
  0% {
    color: rgb(241, 55, 55);
  }
  25% {
    color: rgb(214, 139, 26);
  }
  50% {
    color: rgb(46, 146, 212);
  }
  75% {
    color: rgb(202, 27, 150);
  }
  100% {
    color: rgb(241, 55, 55);
  }
}

.leaderboard-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px;
  max-width: 550px;
  width: 500px;
  height: 250px;
}

.leaderboard-table {
  height: 250px;
  width: "100%";
}

@-webkit-keyframes bounce-7 {
  0% {
    transform: scale(1, 1) translateY(0);
  }
  10% {
    transform: scale(1.1, 0.9) translateY(0);
  }
  30% {
    transform: scale(0.9, 1.1) translateY(-100px);
  }
  50% {
    transform: scale(1.05, 0.95) translateY(0);
  }
  57% {
    transform: scale(1, 1) translateY(-7px);
  }
  64% {
    transform: scale(1, 1) translateY(0);
  }
  100% {
    transform: scale(1, 1) translateY(0);
  }
}

@keyframes bounce-7 {
  0% {
    transform: scale(1, 1) translateY(0);
  }
  10% {
    transform: scale(1.1, 0.9) translateY(0);
  }
  30% {
    transform: scale(0.9, 1.1) translateY(-100px);
  }
  50% {
    transform: scale(1.05, 0.95) translateY(0);
  }
  57% {
    transform: scale(1, 1) translateY(-7px);
  }
  64% {
    transform: scale(1, 1) translateY(0);
  }
  100% {
    transform: scale(1, 1) translateY(0);
  }
}
.clock {
  max-width: 40px;
  max-height: 40px;
}
.image {
  max-width: 50%;
  max-height: 300px;
  justify-content: center;
}
.dropbutton {
  background-color: white;
  color: rgb(63, 75, 84);
  padding: 16px;
  font-size: 16px;
  border: none;
  cursor: pointer;
}
.dropdown {
  position: relative;
  display: inline-block;
  text-align: center;
}
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
#api {
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
}
#celebrity {
  text-align: center;
}

